import "./Equation.css";

let Equation = (props) => {
  let weight = props.weight;
  let height = props.height / 100;
  let bmi;
  let idealWeight = ((48 + 1.1 * (height*100 -150 ) )+ (48 + 0.9 * (height*100 -150 )))/2 ; // Get ideal weight

  if (weight > 1 && weight <= 200 && height > 0.01 && height <= 2) {
    bmi = (weight / (height * height)).toFixed(1);
    props.onCalculate(bmi);
    return <p className="bmi-outupt">
      Your BMI is: <b>{bmi}</b>, Your ideal weight should be ~<span className="ideal-wight">{idealWeight}KG</span>
      </p>;

  } else {
    props.onCalculate(bmi);
    return <p className="bmi-outupt">"Please enter correct values"</p>;
  }

};

export default Equation;
