import React, { useState } from "react";
import "./CalcInputs.css";
import Equation from "./Equation";

let CalcInputs = (props) => {
  const [weight, enterWeight] = useState(props.weight);
  const [height, enterHeight] = useState(props.height);

  let submitValues = () => {
    enterWeight(document.getElementById("weight").value);
    enterHeight(document.getElementById("height").value);
  };

  // Detect body type
  const onCalculateHandler = (calculatedValue) => {
    // Reset all types for after each calculation
    document
      .querySelectorAll(".state")
      .forEach((x) => x.classList.remove("current"));


   if(calculatedValue < 18.5){
      console.log("Underweight");
      document.querySelectorAll(".state")[0].classList.add('current');
    }
    if(calculatedValue >= 18.5 && calculatedValue < 25 ){
        console.log("Normal");
        document.querySelectorAll(".state")[1].classList.add('current');

      }
      if(calculatedValue >= 25 && calculatedValue < 30 ){
        console.log("Overweight");
        document.querySelectorAll(".state")[2].classList.add('current');

      }
      if(calculatedValue >= 30 && calculatedValue < 35 ){
        console.log("Obese");
        document.querySelectorAll(".state")[3].classList.add('current');

      }
      if(calculatedValue > 35 ){
        console.log("Extremely obese");
        document.querySelectorAll(".state")[4].classList.add('current');

      }

  };

  return (
    <div className="main-bmi-calc">
      <div className="input-group mb-3">
        <span className="input-group-text" id="basic-addon1">
          Your weight in KG
        </span>
        <input
          onChange={submitValues}
          type="number"
          className="form-control"
          placeholder="Weight"
          id="weight"
        />
      </div>

      <div className="input-group mb-3">
        <span className="input-group-text" id="basic-addon1">
          Your height in CM
        </span>
        <input
          onChange={submitValues}
          type="number"
          className="form-control"
          placeholder="Height"
          id="height"
        />
      </div>

      <Equation
        onCalculate={onCalculateHandler}
        weight={weight}
        height={height}
      ></Equation>
    </div>
  );
};

export default CalcInputs;
